import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
//import View from '@vkontakte/vkui/dist/components/View/View';
import { View, Panel, PanelHeader, Header, Group, CellButton, Root } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

class App extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            activeView: 'view1'
        }
    }
    render() {
        return (
          <Root activeView={this.state.activeView}>
            <View activePanel="panel-1-1" id="view1">
              <Panel id="panel-1-1">
                <Group>
                  <CellButton onClick={
                      () => this.setState({ activeView: 'view2' })
                    }>
                    Open View 2
                  </CellButton>
                </Group>
              </Panel>
            </View>
            <View activePanel="panel-2-1" id="view2">
              <Panel id="panel-2-1">
                <Group>
                  <CellButton onClick={
                      () => this.setState({ activeView: 'view1' })
                    }>
                    Open View 1
                  </CellButton>
                </Group>
              </Panel>
            </View>
          </Root>
        )
    }
}



export default App;

